<template>
  <div class="sign-up">
      <p>Let's create a new account!</p>
        <div class="form">
            <div class="jumbotron">
                <form @submit.prevent="validateForm">
                <div class="form-group">
                    <input type="text" class="form-control" ref="name" placeholder="Full Name" v-model="user.name" name="Full Name" v-validate="{ required: true}">
                    <p class="text-danger" v-if="errors.has('Full Name')">{{ errors.first('Full Name') }}</p><br>
                    <input type="text"  class="form-control" name="Email" v-model="user.email" placeholder="Email" v-validate="{ required: true, email: true, regex: /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/ }">
                    <p class="text-danger" v-if="errors.has('Email')">{{ errors.first('Email') }}</p><br>
                    <input type="password" class="form-control" v-model="user.password1" placeholder="Password" name="Password" v-validate="{ required: true}">
                    <p class="text-danger" v-if="errors.has('Password')">{{ errors.first('Password') }}</p><br>
                    <input type="password" class="form-control" ref="password2" v-model="user.password2" placeholder="Confirm password" name="Confirm password" v-validate="{ required: true}">
                    <p class="text-danger" v-if="errors.has('Confirm password')">{{ errors.first('Confirm password') }}</p><br>
                    <datepicker   :input-class="'form-control'" :placeholder="'BirthDate'" v-model="user.date"></datepicker><br>
                    <p>{{user.date}}</p>
                    <input type="text" class="form-control" name="url" ref="url" v-model="user.url" placeholder="Facebook URL" v-validate="{ required: false, url: true, regex: /^(https?:\/\/)?([\da-z\.-]+)\.(facebook.com\/)(.)/ }">
                    <p class="text-danger" v-if="errors.has('url')">{{ errors.first('url') }}</p><br>
                    <input type="text" class="form-control" name="phone"ref="phone" v-model="user.phone" placeholder="Phone number" v-validate="{regex:/^\d{10}$/}">
                    <p class="text-danger" v-if="errors.has('phone')">{{ errors.first('phone') }}</p><br>
                </div>
                    <button  class="btn btn-primary" type="submit">Sign Up</button>
                </form>
        
       
  
        
      <!-- <span>or go back to <router-link to="/login">login</router-link>.</span> -->
            </div>
        </div>
      
  </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'
import Datepicker from 'vuejs-datepicker';
import { eventBus } from '../main'

var md5 = require('md5');
let usersRef = db.ref('users');

export default {
  name: 'signUp',
  components:{
            datepicker : Datepicker,
            
        },
  data() {
      return {
          user: {
              name: '',
              email: '',
              password1: '',
              password2: '',
              url: '',
              phone: '',
              date:'',

          },


      }
  },
  methods: {

        
      validateBeforeSubmit() {
          if (this.validateForm() && this.user.email && (this.user.password1 === this.user.password2)) {
              this.signup();
          } else {
              if(!this.user.name) {
                  alert('Name cannot be empty');
                  this.$refs.name.focus();
                  return;
              }
              if(!this.user.email) {
                  alert('Email cannot be empty');
                  this.$refs.email.focus();
                  return;
              }
              if(this.user.password1 != this.user.password2) {
                  alert('Password mismatch');
                  this.$refs.password2.focus();
                  return;
              }
          }
      },
      validateForm(){
          //validate email
            this.$validator.validateAll();
            if (!this.errors.any()) this.signup();

      },
      signup() {
            firebase.auth().createUserWithEmailAndPassword(this.user.email, this.user.password1).then( 
                user => {
                    alert('Your accound has been created!');

                    /* add user info to database */
                    var newUser = this.addUserInfo();
                    eventBus.$emit('isLogin',true);
                    this.$router.push({ name: 'Hello'});
                }, 
                error => {
                    var errorCode = error.code;
                    var errorMsg = error.message;
                    if (errorCode == 'auth/weak-password') {
                        alert('The password is too week');
                    } else {
                        alert('Oops.' + errorMsg);
                    }
                }
            );
      },
      addUserInfo() {
          var newUser = {
              name: this.user.name,
              email: this.user.email,
              phone: this.user.phone,
              url: this.user.url,
              date: this.user.date.toString(),
          }

          var uid = md5(this.user.email);
          console.log(newUser);
          db.ref('users/'+ uid).set(newUser);

      }
    }
}
</script>

<style scoped>
    .signUp {
        margin-top: 40px;
    }
    input {
        margin: auto;
        /* margin: 5px 0; */
        width: 80%;
        padding: 15px;
    }
    button {
        margin-top: 10px;
        /* width: 10%; */
        cursor: pointer;
    }
    span {
        display: block;
        margin-top: 20px;
        font-size: 11px;
    }
    div.vdp-datepicker{
        margin: auto;
        width: 80%;
    }
    p{
        font-size: 15px
    }

</style>




