<template >
  <div class="form">
    <div class="jumbotron">
    <h2>Sign in</h2>
    <form @submit.prevent>
        <div class="form-group">

          <input type="text" class="form-control" placeholder="Username" v-model="user.username">
        </div>
        <div class="form-group">
          <input type="password" class="form-control" placeholder="Password" v-model="user.password">
        </div>


        <button class="btn btn-primary" type="submit" @click="login">Login</button>
      </form>
    </div>
      <p>You don't have an account? You can <router-link to="/signup"> create one</router-link><br>
        </router-link></p>
        <!-- Or... You can <router-link to="/signup2"> create account here -->
      <hr>
      <!-- <p>Sign-in with <span @click="loginGoogle">Google</span> or <span @click="loginFacebook">Facebook</span> account.</p> -->
      <!-- <p>Sign-in with Firebase UI <router-link to='/firebaseauth'>click here</router-link></p> -->

  </div>
</template>

<script>
import firebase from 'firebase';
import { eventBus } from '../main'

export default {
    name: 'login',
    data() {
        return {
            user: {
                username: '',
                password: ''
            },
            isLogin:false

        }
    },
    methods: {
        login() {
            firebase.auth().signInWithEmailAndPassword(this.user.username, this.user.password).then(
                user => {
                    alert('User authentication successful');
                    console.log('go to Form');
                    this.isLogin = true;
                    eventBus.$emit('isLogin', this.isLogin);
                    this.$router.push({name: 'Form'});
                },
                err => {
                    if (err.code === 'auth/wrong-password') {
                        alert('Wrong password.');
                    } else if (err.code === 'auth/user-not-found') {
                        alert('User not found!');
                    } else if (err.code === 'auth/invalid-email') {
                        alert('Invalid email');
                    } else {
                        alert('Oops. ' + err.message);
                    }
                    
                }
            ).catch (error => {
                if (error.code === 'auth/wrong-password') {
                    alert('Wrong password')
                } else {
                    alert(error.message);
                }
            })
        },
        loginGoogle() {
            console.log("Google login");
            // Using a popup.
            var provider = new firebase.auth.GoogleAuthProvider();
            provider.addScope('profile');
            provider.addScope('email');
            firebase.auth().signInWithPopup(provider).then(
                result => {
                    if (result) {
                        // This gives you a Google Access Token.
                        var token = result.credential.accessToken;
                        // The signed-in user info.
                        var user = result.user;

                        console.log('token: ' + token);
                        console.log(user);

                        this.$router.push('/hello');
                    }
                    
                },
                err => {
                    alert('Oops. ' + err.message);
                }
            );
        },
        loginFacebook() {
            console.log("Facebook login");
            var provider = new firebase.auth.FacebookAuthProvider();
            provider.addScope('public_profile');
            provider.setCustomParameters({
                'display': 'popup'
            });
            
            firebase.auth().signInWithPopup(provider).then(
                result => {
                    if (result) {
                        // This gives you a Google Access Token.
                        var token = result.credential.accessToken;
                        // The signed-in user info.
                        var user = result.user;

                        console.log('token: ' + token);
                        console.log(user);

                        this.$router.push('/hello');
                    }
                    
                },
                err => {
                    alert('Oops. ' + err.message);
                }
            );        
        }
    }
}
</script>

<style scoped>
    h3 {
        text-align: left;
        font-size: 16px;
    }

</style>



