
<template>

            <div id="header">
                <hr>
                
                <ul class="nav nav-pills nav-justified">
                    <router-link to="/about" tag="li" active-class="active" exact><a>About</a></router-link>
                    <router-link to="/bookmarks"  tag="li" active-class="active" exact><a>Bookmarks</a></router-link>
                    <router-link to="/profile"  tag="li" active-class="active" exact match><a>Profile</a></router-link>
                   <router-link to="/login"  @click.native="isSignin()" tag="li" active-class="active" exact ><a>{{signText}}</a></router-link>
                </ul>
                
                <hr>

            </div>

</template>

<script>
import firebase from 'firebase'
import { eventBus } from '../main'

export default {
    data() {
        return{
          signText:'Sign-in',
        }
      },
  methods:{
      isSignin(){
          var currentUser = firebase.auth().currentUser;    
          if(currentUser){
              if(this.signText == 'Sign-out'){
                    this.logout()
                    // this.signText = 'Sign-in'
              } 
              else this.signText = 'Sign-out'   
          } 
         

      },
      logout(){
        firebase.auth().signOut().then( () => {
        this.signText = 'Sign-in'
        this.$router.push( { name: 'Login'});
      })
      }
  },
          created() {
            
            eventBus.$on('isLogin', (isSignin) => {
            if(isSignin) this.signText = 'Sign-out'
            console.log(isSignin)
            console.log(this.signText)
            // this.isSignin();
      })
        }
}
</script>


<style scoped>
#header {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



