<template  v:onload="fetchData">
  <div class="form">

    <div class="jumbotron">
    <h2>Bookmark Your Favorite Sites</h2>
    <form @submit.prevent="validation">
        <div class="form-group" :class="{'has-error': errors.has('site name') }">
          <label class="control-label" for="site name">Site Name</label>
          <input type="text" class="form-control" name="site name" placeholder="Website Name" v-model="site.siteName" v-validate
            data-vv-rules="required|min:3">
          <p class="text-danger" v-if="errors.has('site name')">{{ errors.first('site name') }}</p>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('site url') }">
          <label class="control-label" for="site url">Site URL</label>
          <input type="text" class="form-control" name="site url" placeholder="Website URL" v-model="site.siteUrl" v-validate
            data-vv-rules="required|url">
          <p class="text-danger" v-if="errors.has('site url')">{{ errors.first('site url') }}</p>
        </div>
        <button class="btn btn-primary" type="submit">Submit</button>
      </form>
</div>
      <div class="well" v-for="s in sites" :key="s.key">
             <h3> {{ s.siteName}}
                 <a class="btn btn-default" target="_blank" v-bind:href="'http://'+s.siteUrl">Visit</a>
                <a class="btn btn-danger" @click="deleteBookmark(s.key)">Delete </a>
            </h3>
      </div>
    

  </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'

var md5 = require('md5');

export default {
  name: 'Form',
  data () {
    return {
      msg: 'Firebase Authentication tutorial',
      user: {
        uid: '',
        photoURL: '',
        userId: '',
        name: '',
        email: '',
        url: '',
        phone: ''
      },
        site: {
        siteName: '',
        siteUrl: '',
        },
        sites:[]
    }
  },
  methods: {
    logout() {
      firebase.auth().signOut().then( () => {
        this.$router.push( { name: 'Login'});
      })
    },
    validation(){
      // console.log(this.user.userId);
      this.$validator.validateAll();
      if (!this.errors.any()) this.submitted();
    },
    
     submitted(event) {
      var uid = this.user.uid;
      this.$http.post('users/'+ uid + '/bookmark.json', this.site)
      .then(response => {         // define how to deal with the response
          console.log(response);
          this.site.siteName = '';
          this.site.siteUrl = '';
          this.fetchData();
          }, error => {               // define how to deal with error
          console.log(error);
          });

        },
    fetchData() {
        console.log('fetch')
        var uid = this.user.uid;
            this.$http.get('users/'+ uid + '/bookmark.json')
                .then(response => {
                    const data = response.json();
                    return data
                    // console.log(data);      // this prints the promise object NOT the data object
                })
                // , error => {
                //     console.log(error);
                // })
                .then(data => {
                    const resultArray = [];
                    for (let key in data){
                        var temp = new Object()
                        temp['key'] = key
                        temp['siteName'] = data[key].siteName
                        temp['siteUrl'] = data[key].siteUrl
                        resultArray.push(temp)
                    }
                    this.sites = resultArray;
                    console.log(this.sites)
                });
            },
      deleteBookmark(key){
        var uid = this.user.uid;
                this.$http.delete('users/'+ uid + '/bookmark/' + key + '.json')
                .then(response => {
                    this.fetchData();
                    return response;           
                },error => {               
                    console.log(error);
                })
        },
  },
  created() {
    
    var user = firebase.auth().currentUser;
    if (user) {
      this.user.photoURL = user.photoURL;
      this.user.userId = user.uid;
      this.user.name = user.displayName;
      this.user.email = user.email;


      var uid = md5(user.email);
      this.user.uid = uid;
      console.log(uid);
      

      db.ref('users/' + uid).once('value').then(
        snapshot => {
          if (snapshot) {
            console.log(snapshot);
            this.user.name = snapshot.val().name;
            this.user.url = snapshot.val().url;
            this.user.phone = snapshot.val().phone;
          }
      });

    }
    this.fetchData();
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  /* color: #42b983; */
}
p{
  font-size: 15px
}
</style>



