<template>
  <div class="hello">
    <h2>User profile</h2>
    <div v-if="user.photoURL">
    <p>Profile Photo:</p>
    <img :src="user.photoURL" style="height: 120px">
    </div>
    <br>
    <p>ID: {{ user.userId }}</p>
    <p v-if="user.name">Name: {{ user.name }}</p>
    <p>Email: {{ user.email }}</p>
    <p>Phone: {{ user.phone }}</p>  
    <p>Facebook: {{ user.url }}</p> 
    <p>Birth Date:{{user.date}}</p>   
    <!-- <button @click="logout">Logout</button> -->
  </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'

var md5 = require('md5');

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Firebase Authentication tutorial',
      user: {
        uid: '',
        photoURL: '',
        userId: '',
        name: '',
        email: '',
        url: '',
        phone: '',
        date:'',
      },
        site: {
        siteName: '',
        siteUrl: '',
        },
        sites:[]
    }
  },
  methods: {
    logout() {
      firebase.auth().signOut().then( () => {
        this.$router.push( { name: 'Login'});
      })
    },
    validation(){
      // console.log(this.user.userId);
      this.$validator.validateAll();
      if (!this.errors.any()) this.submitted();
    },
    
     submitted(event) {
      var uid = this.user.uid;
      this.$http.post('users/'+ uid + '/bookmark.json', this.site)
      .then(response => {         // define how to deal with the response
          console.log(response);
          this.site.siteName = '';
          this.site.siteUrl = '';
          this.fetchData();
          }, error => {               // define how to deal with error
          console.log(error);
          });

        },
    fetchData() {
        var uid = this.user.uid;
            this.$http.get('users/'+ uid + '/bookmark.json')
                .then(response => {
                    const data = response.json();
                    return data
                    // console.log(data);      // this prints the promise object NOT the data object
                })
                // , error => {
                //     console.log(error);
                // })
                .then(data => {
                    const resultArray = [];
                    for (let key in data){
                        var temp = new Object()
                        temp['key'] = key
                        temp['siteName'] = data[key].siteName
                        temp['siteUrl'] = data[key].siteUrl
                        resultArray.push(temp)
                    }
                    this.sites = resultArray;
                    console.log(this.sites)
                });
            },
  },
  created() {

    var user = firebase.auth().currentUser;

    if (user) {
      this.user.photoURL = user.photoURL;
      this.user.userId = user.uid;
      this.user.name = user.displayName;
      this.user.email = user.email;
      this.user.date = user.date;


      var uid = md5(user.email);
      this.user.uid = uid;
      console.log(uid);
      

      db.ref('users/' + uid).once('value').then(
        snapshot => {
          if (snapshot) {
            console.log(snapshot);
            this.user.name = snapshot.val().name;
            this.user.url = snapshot.val().url;
            this.user.phone = snapshot.val().phone;
            this.user.date = snapshot.val().date;
          }
      });

    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



